<template>
	<div class="p">
		<div class="left-bg">
			<div class="comMsg">
				<img src="../assets/img/title/kf-hjzx.png" class="img-title"/>
				<img src="../assets/img/icon/kf-text.png"/>
				<img src="../assets/img/title/kf-fwlx.png" class="img-title"/>
				<img src="../assets/img/icon/kf-pie.png"/>
				<img src="../assets/img/title/fw-wyfw.png" class="img-title"/>
				<el-row :gutter="20">
					<el-col :span="12">
						<div class="grid">
							<p>报修服务<big style="padding-left: 1em;">11</big>次</p>
						</div>
					</el-col>
					<el-col :span="12">
						<div class="grid">
							<p>安全检查<big style="padding-left: 1em;">28</big>次</p>
							
						</div>
					</el-col>
				</el-row>
				
			</div>
		</div>
		<div class="right-bg">
			<div class="safe-width" style="float: right;margin-right: 40px;padding-top: 180px;">
				<img src="../assets/img/title/fw-fhqfw.png"/>
				<div class="CarTotal">
					<el-row :gutter="15">
						<el-col :span="8">
							<percent-pie ids="myCar" class="percent" :data="carTotal" unit='间' :total="80"></percent-pie>
							<p>{{carTotal[0].name}}</p>
						</el-col>
						<el-col :span="8">
							<percent-pie ids="carEd" class="percent"  :data="carEd" unit='间' :total="80"></percent-pie>
							<p>{{carEd[0].name}}</p>
							
						</el-col>
						<el-col :span="8">
							<percent-pie ids="caring" class="percent"  :data="caring" unit='次' :total="161"></percent-pie>
							<p>{{caring[0].name}}</p>
						</el-col>
					</el-row>
				</div>
				<img src="../assets/img/title/fw-xmpsxt.png"/>
				<div class="carData percent-box" v-for="(item,index) in carData">
					<p><label>{{item.name}}</label><span><big>{{item.value}}</big><small>{{item.unit}}</small></span></p>
					<div class="line" style="margin-top: 2px;">
						<div class="line-act" :style="'max-width:100%;'"></div>
					</div>
				</div>
				<div class="echartsline" style="height: 300px;">
					<myLine ids="line3" ref="line3" :data="lineData"></myLine>
				</div>

			</div>
			

		</div>
	</div>	
</template>

<script>
import myLine from '@/components/module/echarts/echartLine.vue'
	import circleHalf from '@/components/module/echarts/circleHalf'
	import percentPie from '@/components/module/echarts/percent'
	export default{
		components:{
			circleHalf,
			percentPie,myLine
		},
		data(){
			return{
				carTotal:[{
				    'name': '会议室预订数量',
				    'value': 60
				}],
				carEd:[{
				    'name': '会议室剩余数量',
				    'value': 20
				}],
				caring:[{
				    'name': '服务企业次数',
				    'value': 161
				}],
				carData:[{
				name:'当前服务企业总数',
				value:620,
				'unit':'家'
				},{
					name:'累计服务企业总数',
					value:1000,
					'unit':'家'
				}],
				lineData:{
					series:[{
				    	symbol:'none',
				    	symbolSize:0,
				    	name:"提交申请项目总数",
				        data: [461,552,453,534,421,452,552,453,534,421,452],
				        type: 'line',
				        areaStyle:{
				        	normal:{
				        		color: {
								    type: 'linear',
								    x: 0,
								    y: 1,
								    x2: 0,
								    y2: 0,
								    colorStops: [{
								        offset: 0, color: 'rgba(3,92,255,.2)' // 0% 处的颜色
								    }, {
								        offset: 1, color: 'rgba(2,228,255,0.2)' // 100% 处的颜色
								    }],
								    globalCoord: false // 缺省为 false
								}
							}			
						},
						lineStyle:{
							color: {
							    type: 'linear',
							    x: 0,
							    y: 0,
							    x2: 1,
							    y2: 0,
							    colorStops: [{
							        offset: 0, color: '#02e4ff' // 0% 处的颜色
							    }, {
							        offset: 1, color: '#035cff' // 100% 处的颜色
							    }],
							    globalCoord: false // 缺省为 false
							}
						},
		        	smooth: true
			    },
					{
				    	symbol:'none',
				    	symbolSize:0,
				    	name:"通过项目总数",
				        data: [602,652,553,634,581,681,652,553,634,581,681],
				        type: 'line',
				        areaStyle:{
				        	normal:{
				        		show:false,
				        		color: {
								    type: 'linear',
								    x: 0,
								    y: 1,
								    x2: 0,
								    y2: 0,
								    colorStops: [{
								        offset: 0, color: 'rgba(2,255,56,0)' // 0% 处的颜色
								    }, {
								        offset: 1, color: 'rgba(2,228,255,0)' // 100% 处的颜色
								    }],
								    globalCoord: false // 缺省为 false
								}
							}			
						},
						lineStyle:{
							color: {
							    type: 'linear',
							    x: 0,
							    y: 0,
							    x2: 1,
							    y2: 0,
							    colorStops: [{
							        offset: 0, color: '#02e4ff' // 0% 处的颜色
							    }, {
							        offset: 1, color: '#02ff38' // 100% 处的颜色
							    }],
							    globalCoord: false // 缺省为 false
							}
						},
		        	smooth: true
			    }],
					dataAxis:['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月'],
					unit:''
				},
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-top: 170px;
		margin-right: 40px;
		float: right;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& h3,label{
			font-size: 24px;
		}
		& span{
			font-size: 20px;
		}
		& p{
			margin: 15px 0;
		}
	}
	.echartline{
		height: 250px;
		width: 100%;
	}
	.buildMsg{
		width: 100%;
		p{border-bottom: 2px solid rgba(255,255,255,.24);text-align: right;}
		& span{
			width: 10em;
			font-size: 22px;
			display: inline-block;
			text-align: left;
	        margin-top:10px;
			float: left;
		}
		& label{
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        font-size: 40px;
			width: 3em;
			display: inline-block;
			font-weight: bold;
			text-align: left;
			font-family: 'DINOT';
		}
		& small{
			font-size: 20px;
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        width: 3em;
	        display: inline-block;
	        text-align: right;
	        float: right;
	        margin-top:10px;
	        font-family: 'DINOT';

		}
	}
	.percent-box{
		img{width: 100%;}
	}
	.comMsg{
		margin-left:40px;
	}
	.p h5{
		font-size: 16px;
		 background: linear-gradient(to top, #00c0ff, #0090ff);
        -webkit-background-clip: text;
        color: transparent;
       	padding-left: 1.4em;
       	margin-bottom: 10px;
	}
	.comMsg p{
		color: #fff;
	}
	.CarTotal{
		margin: 10px 0 40px;
		width: 100%;
		height: auto;
		p{
			font-size:17px;
			color: #FFFFFF;
			text-align: center;
		}
		.percent{
			height: 150px!important;
		}
	}
	.grid{
		background: url(../assets/img/2.png)no-repeat;
		background-size: 100%;
		width: 100%;
		height: 68px;
		display: inline-block;
		margin-top: 30px;
		padding: 5px 10px;
	}
	.grid p{
		font-size: 18px;
		color: #FFFFFF;
	}
	.grid big{
		background: linear-gradient(to top, #0072ff, #00fff0);
        -webkit-background-clip: text;
		color: transparent;
		font-size: 38px;
		padding: 5px;
	}
	
	.carData{
		color: #FFFFFF;

	}
	.carData p{
		font-size: 18px;
		height: 60px;
		text-align: left;
		vertical-align: baseline;
	}
	.carData span{
		float: right;
	}
	.carData label{
		display: inline-block;
		padding-top:15px;
		
	}
	.carData::after{
		clear: both;
	}
	.carData big{
		font-size: 40px;
		font-weight: normal;
		margin-right: 10px;
	}
	.carData .line{
		height: 1px;
		background: rgba(255,255,255,.3);
		position: relative;
	}

</style>


